*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  background: url("../img/bg.jpg") no-repeat center / cover;
}
.container {
  max-width: 1920px;
  width: 100%;
  height: 100%;
  padding: 2% 10%;
}
.weather {
  height: 75%;
  margin-bottom: 3%;
  h1 {
    text-align: center;
    font-size: calc((100vw + 747px) / 26.666);
    margin-bottom: 2%;
    color: rgba(19, 50, 114, 0.52);
  }
  &__list {
    display: flex;
    align-content: space-around;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    min-height: 60%;
    width: 100%;
    list-style: none;
    li {
      flex-basis: 19%;
      display: flex;
      flex-direction: column;
      align-items: center;
      border: 1px solid cornflowerblue;
      border-radius: 5px;
      color: rgba(20, 20, 23, 0.7);
      box-shadow: 2px 1px 9px 0px rgba(50, 50, 50, 0.81);
      &.hidden {
        display: none;
      }
      h3 {
        display: block;
        width: 100%;
        text-align: center;
        padding: 5px;
        background-color: rgba(255, 255, 0, 0.51);
      }
      strong {
        display: block;
        width: 100%;
        padding: 5px;
        text-align: center;
        background-color: rgba(255, 255, 0, 0.51);
      }
    }
  }
  .weather__name {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-content: space-around;
    justify-content: space-around;
    width: 100%;
    list-style: none;
    background-color: rgba(158, 119, 61, 0.51);
    color: #fff;
    font-size: 22px;
    height: 150px;
    border-radius: 5px;
    li {
      display: block;
      text-align: center;
      width: 100px;
      padding: 20px;
      transition: all 0.3s linear;
      cursor: pointer;
      box-shadow: 2px 1px 9px 0px rgba(50, 50, 50, 0.81);
      &:hover {
        color: #3be7d4;
        background-color: mintcream;
      }
    }
  }
  input {
    display: inline-block;
    width: 80%;
    height: 40px;
    padding-left: 10px;
    border: none;
    border-radius: 5px;
    outline: none;
  }
  button {
    display: inline-block;
    height: 40px;
    width: 18%;
    margin-left: 1%;
    border: none;
    border-radius: 5px;
    outline: none;
    background-color: #3be7d4;
    transition: all 0.3s linear;
    &:hover {
      cursor: pointer;
      background-color: #127b6f;
    }
  }
}

#map {
  width: 100%;
  height: 150px;
  margin-top: 1%;
  overflow: hidden;
}
